@media (max-width: 480px) {
    #parent_Box {
        height: 50px;
    }

    #name_Box {
        width: 20%;
        margin: auto;
        margin-left: -15px;
    }

    #pages_Box {
        height: 100%;
        width: 90%;
        margin-right: -15px;
    }

    #pages_Box>a {
        font-size: 9px;
        /* border: 1px solid greenyellow; */
    }

    .download_resume {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 30px;
        width: 50px;
        background-color: rgb(223, 23, 173);
        border-radius: 10px;
        font-size: 9px;
        font-weight: 600;
        color: black;
    }
     #theme_Button{
        width: 10px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
        margin-right: 10px;
    }
     #theme_Button>Button{ 
        width: 50%;
    }
   
}

@media (min-width: 481px) and (max-width: 768px) {

    #name_Box {
        width: 20%;
        margin: auto;
        margin-left: -15px;
    }

    #pages_Box {
        width: 80%;
    }

    #pages_Box>a {
        font-size: 13px;
    }

    .download_resume {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 30px;
        width: 70px;
        background-color: rgb(223, 23, 173);
        border-radius: 10px;
        font-size: 13px;
        font-weight: 600;
        color: black;
    }
}

@media (min-width: 769px) and (max-width: 1200px) {

    #name_Box {
        margin-top: auto;
        margin-bottom: auto;

    }

    #pages_Box {
        width: 70%;
    }

    .download_resume {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 40px;
        width: 80px;
        background-color: rgb(223, 23, 173);
        border-radius: 10px;
        font-size: 15px;
        font-weight: 600;
        color: black;
    }
}

@media (min-width: 1201px) {
    .download_resume {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 40px;
        width: 130px;
        background-color: rgb(223, 23, 173);
        border-radius: 10px;
        font-size: 18px;
        font-weight: 500;
        color: black;
    }
}